.sidebar {
    flex: 3;
    height: fit-content;
    margin: 20px;
    padding-bottom: 30px;
    background-color: #fdfbfb;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgb(255, 251, 251);
    width: 100%;
    /* background-color: black; */
}

.sidebarItem {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.sidebarTitle {
    margin: 10px;
    padding: 5px;
    width: 100%;
    border-top: solid 1px #a7a4a4;
    border-bottom: solid 1px #a7a4a4;
    text-align: center;
    font-family: "Varela Round", sans-serif;
    font-size: 12px;
    line-height: 19px;
    /* color: #222222; */
    font-weight: 600;
    color: rgb(49, 47, 47);
}

/* .sidebarItem > img {
    margin-top: 15px;
    width: 250px;
    height: 400px;
} */

.sidebarItem > p {
    padding: 5px;
}

.sidebarList {
    list-style-type: none;
    /* margin-bottom: 30px; */
    display: flex;
    justify-content: center;
}
.sidebarList:hover {
    cursor: pointer;
    transform: scale(1.1);
}
.sidebarListItem {
    display: flex;
    margin: 20px;
    width: 50%;
    margin-top: 15px;
    cursor: pointer;
}

.sidebarSocial {
    margin-top: 15px;
    width: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: black;
}
.colorIcon {
    color: black;
}
.sidebarIcon {
    font-size: 16px;
    margin-left: 10px;
}
.Linkcat {
    color: black;
}
.Linkcat:hover {
    color: tomato;
}
